<template>
  <div class="login reset">
    <header class="mui-bar mui-bar-nav">
      <a class="mui-action-back mui-icon mui-icon-left-nav mui-pull-left" href="/"></a>
      <h1 class="mui-title"></h1>
    </header>
    <div class="mui-content">
      <form id='login-form' class="mui-input-group"  @submit.prevent="submit">
        <div class="mui-input-row">
          <label><i class="jp-ico_telepohone"></i></label>
          <input style="width: 54%;margin-right:30%;" name="username" id='account' type="text" class="mui-input-clear mui-input" placeholder="" v-model="form.username">
          <a class="sms" href="#"></a>
        </div>
        <div class="mui-input-row">
          <label><i class="jp-ico_verification_code"></i></label>
          <input name="password" id='password' type="password" class="mui-input-clear mui-input" placeholder="" v-model="password">
        </div>
        <div class="mui-input-row">
          <label><i class="jp-ico_password"></i></label>
          <input name="password" id='password' type="password" class="mui-input-clear mui-input" placeholder="" v-model="password">
        </div>
        <div class="mui-content-padded">
          <button id='login' class="mui-btn mui-btn-block mui-btn-yell"></button>
        </div>
        <div class="mui-content-padded oauth-area">

        </div>
      </form>
    </div>
  </div>
</template>

<script >
  import { mapActions } from 'vuex'
  import { USER_SIGNIN } from '../../style/js/user'

  export default {
    data() {
      return {
        btn: false, //true  false
        password: '',
        form: {
          token:'',
          username: ''
        }
      }
    },

    methods: {
      ...mapActions([USER_SIGNIN]),
      submit() {
        //
        this.btn = true
        if(!this.form.username && !this.password) {
          mui.toast('')
          return
        }
        if(!this.form.username){
          mui.toast('')
          return
        }
        if(!this.password){
          mui.toast('')
          return
        }

        //token
        this.$http.post('/api/user/login', {username:this.form.username,password:this.password}).then(response=>{
          // token
          if(response.body.state === 'fail'){ // statefail
            mui.toast('')
            return false
          }
          //token
          this.form.token = response.body.data.token
          this.$http.post('/api/user/user', {token: this.form.token}).then(response => {
            // 
            this.USER_SIGNIN(this.form)
            mui.toast('')
            this.$router.replace({path: '/personal'})
          })

        },response => {
          console.log('1')
        })

      }
    }
  }
</script>





