import Vue from 'vue'
import Router from 'vue-router'
import store from '../../static/js/store'

import Home from 'components/home/home'
import Details from 'components/details/details'
import Dotask from 'components/details/dotask'
import Personal from 'components/personal/personal'
import Withdrawal from 'components/personal/withdrawal'
import Balance from 'components/personal/balance'
import Account from 'components/personal/account'
import Profile from 'components/personal/profile'
import Task from 'components/personal/task'
import Login from 'components/login/login'
import Registered from 'components/login/registered'
import Reset from 'components/login/reset'


Vue.use(Router)

const title = ''

const router =  new Router({
  mode: 'hash',//  : "hash" | "history" | "abstract""hash"
  linkActiveClass: 'active', // class
  routes: [
    {
      path: '/',
      meta: {
        title: '',
        auth: false
      },
      component: Home
    },
    {
      path: '/home',
      name: 'Hello',
      meta: {
        title: '',
        auth: false
      },
      component: Home
    },
    {
      path: '/details/:userId',
      name: 'Details',
      meta: {
        title : '',
        auth: false
      },
      component: Details
    },
    {
      path: '/dotask/:userId',
      name: 'Dotask',
      meta: {
        title : '',
      },
      component: Dotask
    },
    {
      path: '/personal',
      name:'personal',
      meta: {
        title: '',
      },
      component: Personal
    },
    {
      path : '/withdrawal',
      name: 'Withdrawal',
      meta: {
        title:''
      },
      component : Withdrawal
    },
    {
      path : '/balance',
      name: 'Balance',
      meta: {
        title:''
      },
      component : Balance
    },
    {
      path : '/account',
      name: 'Account',
      meta: {
        title:''
      },
      component : Account
    },
    {
      path : '/profile',
      name: 'Profile',
      meta: {
        title:''
      },
      component : Profile
    },
    {
      path : '/task',
      name: 'Task',
      meta: {
        title:''
      },
      component : Task
    },
    {
      path: '/login',
      component: Login,
      name:'login',
      meta: {
        title: ''
      }
    },
    {
      path: '/registered',
      component: Registered,
      name:'Registered',
      meta: {
        title: ''
      }
    },
    {
      path: '/reset',
      component: Reset,
      name:'Reset',
      meta: {
        title: ''
      }
    }
  ]
})

// beforeEach
router.beforeEach((to, from, next) => {
  let titleStr = ''
  let con=''
  // 
  if (to.name !== 'Home') {
    // title
    for (let i = to.matched.length - 1; i >= 0; i--) {
      con = `${to.matched[i].meta.title}`
      if(i=0){
        con === `${to.matched[0].meta.title}`
      }
      titleStr += con
    }
  }
  // 
  titleStr += title
  // title
  document.title = titleStr

  // auth truefalsetrue
  var { auth = true } =  to.meta
  var isLogin = Boolean(store.state.user.token) //true false
  if (auth && !isLogin && to.path !== '/login') {
    return next({ path: '/login' })
  }


  // 
  next()
})

export default router



