<template>
  <div class="withdrawal">
    <div class="with-wrapper">
      <span class="text"><i class="jp-ico_fund_details"></i>  <span class="red">120.00</span></span>
      <div class="content">
        <span class="money"><input name="money" id="money" value="" type="text" placeholder="0.00"></span>
      </div>
      <span class="remind"><em>*</em> 2/,50</span>
    </div>

    <div class="btn-div" onclick="withdrawal()">
      <button type="button" class="mui-btn" @click="withdrawal"></button>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data() {
     return {

     }
    },
    methods: {
      btnArray() {
        //
        mui.confirm('<span> <br> </span> ','<i class="jp-ico_information_authentication"></i>',new Array('',''),function (e) {
          if (e.index == 1) {
            //mui.toast('');
            location.href ='/user/center/profile.html';
          } else {
            // mui.toast('');
          }
        })
      } ,
      withdrawal() {
        // 
        var money = $("#money").val();
        var fee = 2;
        var real_money = money-fee;
        var account_money = '{$balance.account_money}';
        if(account_money-money<0){
          mui.toast('');
          return false;
        }
        if(money<50){
          mui.toast('50');
          return false;
        }

        mui.confirm('<span> <br> </span> ','<span class="price"><span class="red">'+real_money+'</span></span>',new Array('',''),function (e) {

          if (e.index == 1) {
            $.ajax({
              url: "/user/center/dowithdraw/",
              type: "post",
              dataType: "json",
              data: {"money":money},
              success: function(d) {
                if (d.status == 1) {
                  mui.alert('<span class="suc"></span>', '<i class="jp-ico_customer_service"></i>', '',function() {
                    document.location.href='/user/center';
                  });
                  //mui.toast('');
                }else{
                  mui.toast(d.info);
                }
              }
            });



          } else {
            // mui.toast('');
          }
        })
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .withdrawal
    .with-wrapper
      padding 0.4rem
    .text
      color: #666;
      font-size: 0.4rem;
      i
        vertical-align: middle;
        color: #ffbd65;
        margin-right: 0.133rem;
        font-size: 0.533rem;
      .red
        color: #fb5a5a;
        margin-left: 5px;
    .content
      height: 2.5rem;
      line-height: 2.5rem;
      text-align: center;
      .money
        color: #666;
        font-size: 0.4rem;
        margin-right: 12%;
        input
          width: 30%;
          text-align: right;
          font-size: 0.8rem;
          border: 0;
          padding: 0 0.133rem 0 0;
    .remind
      color: #666;
      font-size: 0.32rem;
      em
        color: #fb5a5a;
    .btn-div
      text-align: center;
      position: fixed;
      left: 0;
      right: 0;
      bottom: 0;
      .mui-btn
        width: 100%;
        height: 1.306rem;
        border-radius: 0px;
        font-size 0.45rem
        border: 0;
        background-color: #fb5a5a;
        color: #fff;


  .price
    font-size 0.6rem
</style>



